.search {
    display: flex;
    height: 20px;
    gap: 5px;
}

.search__btn {
    border: none;
    background: transparent;
    cursor: pointer;
    z-index: 0;

}

.search__btn img {
    width: 20px;
    height: 20px;
    position: relative;
    z-index: -1;
}

.search__input {
    background: transparent;
    border: none;
    border-bottom: 1px solid black;
}

.info {
    display: grid;
    grid-template-columns: 100px 100px;
    grid-template-rows: auto;
    column-gap: 15px;
}